html {
  height: 100%;
  width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(#c0c0c0));
  background: -webkit-linear-gradient(#ffffff, #c0c0c0);
  background: -o-linear-gradient(#ffffff, #c0c0c0);
  background: linear-gradient(#ffffff, #c0c0c0);
  min-height: 100%;
}

#content {
  margin: 8px;
}

#loading {
  margin: 0 auto;
  border: 10px solid #f3f3f3; /* Light grey */
  border-top: 10px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  -webkit-animation: loading-spin 2s linear infinite;
          animation: loading-spin 2s linear infinite;
}

@-webkit-keyframes loading-spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes loading-spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



